import React from "react";
import { useTheme } from "../core/ThemeProvider";
import { useLanguage } from "../core/LanguageProvider";
import { AvatarProvider } from "../core/AvatarProvider";
import Avatar from "./Avatar";
import UserName from "./UserName";
import SetAndShowProperty from "./SetAndShowProperty";

export default function Account() {
  const themeColor = useTheme();
  const language = useLanguage();
  return (
    <div className="flex py-4">
      <div className="w-1/2">
        <AvatarProvider>
          <Avatar />
        </AvatarProvider>
        <UserName unique={true} />
      </div>
      <div className="w-1/2">
        <p className={`${themeColor.colorTextExplane} break-words`}>
          {language.setName}
        </p>
        <SetAndShowProperty property="name" />
        <SetAndShowProperty property="tags" />
      </div>
    </div>
  );
}
