import React from "react";
import { useTheme } from "../core/ThemeProvider";
import { useLanguage } from "../core/LanguageProvider";
import { useUser } from "../core/UserProvider";
import { usePersonList } from "../core/PersonListProvider";
import SetAndShowProperty from "../account/SetAndShowProperty";
import Person from "../person/Person";

export default function PersonList() {
  const themeColor = useTheme();
  const language = useLanguage();
  const user = useUser();
  const personList = usePersonList();

  return (
    <>
      <div className="flex flex-col justify-center">
        <div className={`${themeColor.colorTextExplane}`}>
          {language.closestPeople}
        </div>
        <SetAndShowProperty property="filter" />
      </div>
      <div className="flex flex-wrap justify-around">
        {user.score === 0 ? (
          language.zeroScore
        ) : personList.length !== 0 ? (
          personList.map((person) => {
            return (
              <div className="w-full md:w-1/2 lg:w-1/3 max-w-md px-1">
                <Person
                  key={person._id}
                  personId={person._id}
                  closest={person.closest}
                  comparedImage={person.comparedImage}
                />
              </div>
            );
          })
        ) : (
          <p>{language.emptyPersonList}</p>
        )}
      </div>
    </>
  );
}
